import books from '../../contacts/books.json';
import _, { filter } from 'lodash';
import actions from './actions';



const initialState = {
    limit: 20,
    booksItems: [],
    searchBook: [],
    searching: false,
    isLoad: false,
    similarBooks: []
};

const {
    GET_BOOKS,
    MORE_BOOKS_DATA,
    SEARCH_BOOK_DATA,
    SEND_BOOK_COMMENT,
    GET_SIMILAR_BOOKS
} = actions;

//JSON.parse(books)

export const booksStore = (
    state = initialState,
    {type, payload}
) => {
    switch (type) {
        case GET_BOOKS:
            return {
                ...state,
                booksItems: payload,
                isLoad: true
            }
        case MORE_BOOKS_DATA:
            return {
                ...state,
                limit: state.limit + payload
            }
        case SEND_BOOK_COMMENT:
            let book_id = payload.book_id;
            let user_id = payload.user_id;
            let comment = payload.comment;
            let data = {
                user_id: user_id,
                book_id: book_id,
                comment: comment
            }

            let array = [];
            state.booksItems.map(item => {
                if (item._id === book_id) {
                    item.book_comments.unshift(data)
                }
                array.push(item)
            })

            return {
                ...state,
                booksItems: array
            }
        case GET_SIMILAR_BOOKS: 
        let filterBook = [];
            if (payload !== undefined) {
                
                let filterGenres = [];
                let filterBook = [];

                _.forEach(payload.book_genres, (item, index) => {
                    if (item === true) {
                        filterGenres = {[index]: item  ,...filterGenres}
                    }
                })

                _.forEach(state.booksItems, book => {
                    let findPayload = _.find(filterGenres, genres => book.book_genres[genres] === true);
                    if (findPayload) {
                        filterBook = [book, ...filterBook]
                    }
                })

                console.log(filterBook)

            }
            return {
                ...state,
                similarBooks: []
            }
        case SEARCH_BOOK_DATA:
            if (payload.length !== 0) {
                console.log(state);
                let filterSeacthBook = state.booksItems.filter(item => item.book_name.toLowerCase().includes(payload.toLowerCase()));

                console.log(filterSeacthBook)

                if (filterSeacthBook.length !== 0) {
                    console.log('In ')
                    return {
                        ...state,
                        searchBook: filterSeacthBook,
                        searching: true
                    }
                }else {
                    return {
                        ...state,
                        searchBook: false,
                        searching: true,
                    }
                }
            }else {
                return {
                    ...state,
                    searchBook: [],
                    searching: false
                }
            }

            // if (payload) {
            //     return {
            //         ...state,
            //         searchBook: state.booksItems.filter(item => item.book_name.toLowerCase().includes(payload.toLowerCase()))
            //     }
            // }else {
            //     return {
            //         ...state,
            //         searchBook: []
            //     }
            // }
            
        default:
            return state
    }
}